:root{
    --base: #094147;
    --text: #fff;
}
#logo{
    height: 2rem;
    margin-left: 0.5rem;
    font-size: 1.8rem;
}
#menuX, #menuLines{
    height: 2rem;
    user-select: none;
}
.navbar{
    background: var(--base);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
}

.navbar-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
}
.navbar-logo{
    color: var(--text);
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.85rem;
    display: flex;
    align-items: center;
}

.nav-menu{
    display: grid;
    grid-template-columns: repeat(5,auto);
    grid-gap:1.5rem;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
}
.nav-item{
    height: 80px;
    font-size: 1rem;
}
.nav-links{
    color: var(--text);
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem, 1rem;
    height: 100%;
}
.nav-links:hover{
    border-bottom:4px solid var(--text);
    transition: all 0.2s ease-out;
}
#menuLines{
    color: var(--text);
}

.nav-links-mobile{
    display: none;
}
.menu-icon{
    display: none;
}

@media screen and (max-width:960px){
    .NavbarItems{
        position: relative;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }
    .nav-menu-active{
        background: var(--base);
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }
    .nav-links{
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }
    .nav-links:hover{
        background-color: (--text);
        color: (--base);
        border-radius: 0;
    }
    .navbar-logo{
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(25%, 50%);
    }
    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }
    #menuX{
        color: var(--text);
        font-size: 2rem;
    }
    .nav-links-mobile{
        display: block;
        text-align: center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: var(--text);
        padding: 14px 20px;
        border: 1px solid var(--text);
        transition: all 0.3s ease-out;
    }
    .nav-links-mobile:hover {
        background: var(--text);
        color: var(--base);
        transition: 250ms;
    }
}
